import FHIR from 'fhirclient';

const Events = {};

// Patient events
Events.patient = {};

Events.patient.launch = () => {
    FHIR.oauth2.authorize({
        target: "popup",
        client_id: "efef2e22-0d52-4bfa-ab53-b44456625f2a",
        scope: "user/AllergyIntolerance.read user/CarePlan.read user/Condition.read user/MedicationStatement.read user/Observation.read user/Patient.read user/RelatedPerson.read launch/patient online_access openid profile",
        iss: "https://fhir-myrecord.cerner.com/dstu2/ec2458f2-1e24-41c8-b71b-0e701af7583d",
        redirectUri: "https://danielmoffitt54.github.io/UMX-DX/"
    }).catch(console.error);
}

Events.patient.ready = (setAppState) => {
    FHIR.oauth2.ready()
        .then(client =>  setAppState({ Loading: false, Cerner: client }))
        .catch(err => {
            setAppState({ Loading: false });
            console.log(err);
        });
}

// Provider events
Events.provider = {};

Events.provider.launch = (props) => {
    FHIR.oauth2.authorize({
        target: "popup",
        client_id: "4cfb74e7-deb2-4151-9c22-16eba93fd1ec",
        scope: "patient/Patient.read patient/Observation.read launch online_access openid profile",
        redirectUri: "https://danielmoffitt54.github.io/UMX-DX/"
    }).then(console.log).catch(console.error);
}

Events.provider.ready = (setAppState) => {
    FHIR.oauth2.ready()
        .then(client => client.request("Provider"))
        .then(res =>  setAppState({ Loading: false, Cerner: res }))
        .catch(err => {
            console.log(err);
            setAppState({ Loading: false });
        });
}

export default Events;